
.vimeography-thumbnail-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  justify-content: space-between;
  margin-bottom: 2rem;
}

/* IE11 Support */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .vimeography-thumbnail-container {
    display: flex;
    flex-wrap: wrap;
  }
}
